.titleContainer {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .titleBox {
    display: flex;
    align-items: center;

    img {
      width: 30px;
      height: auto;
      margin-right: 20px;
    }

    h1 {
      color: var(--primaryTitle);
      font-weight: 900;
    }
  }
}

@media only screen and (max-width: 600px) {
  .titleContainer {
    width: 100%;
    margin: 0 2%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .titleBox {
      height: 100%;
      display: flex;
      align-items: center;

      img {
        width: 25px;
        height: auto;
        margin-right: 10px;
      }

      h1 {
        color: var(--primaryTitle);
        font-weight: 900;
        font-size: 1.2rem;
      }
    }
  }
}
